import Vue from 'vue'
import Router from 'vue-router'
import HomeCenter from "@/components/page/Home/HomeCenter";
import BlogCenter from "@/components/page/Blog/BlogCenter";
import MyBlog from "@/components/page/Blog/MyBlog";
import Classification from "@/components/page/Blog/Classification";
import BlogHome from "@/components/page/Blog/BlogHome"
import writeBlog from "@/components/page/Blog/writeBlog";
import BlogVIEW from "@/components/page/Blog/BlogVIEW";
import SearchBlogs from "@/components/page/Blog/SearchBlogs";
import PersonalCenter from "@/components/page/User/PersonalCenter";
import PersonalData from "@/components/page/User/PersonalData";
import AccountSettings from "@/components/page/User/AccountSettings";

Vue.use(Router)

const  router = new Router({
  mode:'history',
  routes:[
    {path:'/',redirect:'/home'},
    {path:'/home',component:HomeCenter,meta:{title:'astronaut'}},
    {path:'/writeBlog',component:writeBlog,meta:{title:'写文章'}},
    {path:'/blogView',component:BlogVIEW,meta:{title:'查看文章'}},
    {path:'/myBlog',component:MyBlog,meta:{title:'我的博客',isAuth:true}},
    {path:'/search',component:SearchBlogs,meta:{title:'搜索文章'}},
    {path:'/blogCenter',component:BlogCenter,meta:{title:'博客中心'},
      redirect:'/blogCenter/blogHome',
      children:[
        {path:'blogHome',component:BlogHome,meta:{title:'博客首页'}},
        {path:'classify',component:Classification,meta:{title:'分类'}}
      ]
    },
    {path:'/userCenter',component:PersonalCenter,meta:{title:'个人中心',isAuth:true},
      redirect:'/userCenter/personalData',
      children:[
        {path:'personalData',component:PersonalData,meta:{title:'用户资料',isAuth:true}},
        {path:'account',component:AccountSettings,meta:{title:'账号设置',isAuth:true}},
      ]
    },
  ]
})

router.beforeEach((to,from,next)=>{
  if(to.meta.isAuth){
    const tokenStr = window.sessionStorage.getItem('token')
    if(!tokenStr){
      return next('/home')
    }
  }
  next()
})
router.afterEach((to,from)=>{
  if(to.meta.title){
    document.title = to.meta.title
  }
})
export  default  router
