<template>
  <div class="container" >
    <div class="editBlog">

      <el-form ref="WriteBlogRef"  :model="blog" :rules="writeBlogRules">
        <el-form-item label="标题:" label-width="100px" prop="title">
          <el-input v-model="blog.title" label="标题" placeholder="请输入文章标题(1-100字)" maxlength="100" show-word-limit style="width: 80%;"></el-input>
          <el-button round style="background-color: salmon;color: white;margin-left: 50px" @click="saveBlog">保存草稿</el-button>
          <el-button round style="background-color: cornflowerblue;color: white;" @click="saveBlog">发布博客</el-button>
        </el-form-item>

        <el-form-item prop="content">
          <mavon-editor
              ref="md"
              placeholder="请输入博客内容..."
              :boxShadow="false"
              v-model="blog.content"
              :toolbars="toolbars"
              toolbarsBackground="#999999"
              @save="saveBlog"
              @imgAdd="imgAdd"
              style="height: 80vh"
          />
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      //博客
      blog:{
        title:'',
        content: '',
      },
      itemDes:"",
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        strikethrough: true, // 中划线
        mark: true, // 标记
        superscript: true, // 上角标
        subscript: true, // 下角标
        quote: true, // 引用
        ol: true, // 有序列表
        ul: true, // 无序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        fullscreen: true, // 全屏编辑
        readmodel: true, // 沉浸式阅读
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 1.3.5 */
        undo: true, // 上一步
        redo: true, // 下一步
        trash: true, // 清空
        save: false, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: true, // 导航目录
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true, // 预览
        writeBlogRules:{

        },
        //编辑博客时需要从其他页面传递blogId
        blogId:''
      },
      writeBlogRules:{
        title:[
          {required:true,message:'请输入文章标题',trigger: 'none'},
          { min: 1,max:100,message: '长度在 1 到 100 个字符', trigger: 'blur' }
        ],
        content:[
          {required:true,message:'请输入文章内容',trigger: 'none'},
          { min: 1,message: '内容不能为空', trigger: 'blur' }
        ]

      }
    }


  },
  created() {
    this.blogId = this.$route.query.blogId
    this.getBlog()
  },
  methods:{
     async getBlog(){
       console.log("blogid",this.blogId)
       const {data : res} = await this.$http.post('/getNowBlog',this.blogId)
       console.log(res)
       this.blog.title = res.data.blog.TITLE
       this.blog.content = res.data.blog.CONTENT
     },
    //保存blog
     saveBlog(){
      console.log(JSON.stringify(this.blog))
      this.$refs.WriteBlogRef.validate(async valid=>{
        if(!valid) return this.$message.error("格式错误!")
        if(this.blogId){
          const {data : res} =await this.$http.post('/saveBlog',{blog:this.blog,blogId:this.blogId} )
          console.log("获取到的响应",res)
          if(res.meta.status===201){
            this.$message.success('发布成功!')
            return this.$router.push('./myBlog')
          }
        }else{
          const {data : res} =await this.$http.post('/saveBlog',{blog:this.blog,blogId:''} )
          console.log("获取到的响应",res)
          if(res.meta.status===201){
            this.$message.success('发布成功!')
            return this.$router.push('./myBlog')
          }
        }
      })

    },
    async imgAdd(pos,$file){
      let formdata = new FormData()
      console.log("filename",$file.name)
      console.log("filename",$file.name)
      formdata.append('file',$file)
      const {data : res} = await this.$http.post('/writeBlog/addImage',formdata)
      console.log(res)
      if(res.meta.status===0){
        return  this.$message.error('上传失败,请先登录!')
      }else if(res.meta.status===1){
        return this.$message.error('上传失败,登录失效,请重新登录!')
      }else{
        let url ='http://192.168.199.140:8090/'+res.data
        let name = $file.name
         if (name.includes('-')) {
         name = name.replace(/-/g, '')
        }
        console.log("name",name)
        let content = this.blog.content
        if(content.includes(name)){
          let oStr = `(${pos})`
          let nStr = `(${url})`
          let index = content.indexOf(oStr)
          let str = content.replace(oStr, '')
          let insertStr = (soure, start, newStr) => {
            return soure.slice(0, start) + newStr + soure.slice(start)
          }
          this.blog.content = insertStr(str, index, nStr)
        }else{
          this.$message.error("上传失败!")
        }
      }
    }
  }
}
</script>

<style scoped Lang="less">
*{
  margin: 0;
  padding: 0;
}
html, body {
  height: 100%;
  width: 100%;
  overflow: hidden;
}

body {
  position: absolute;
  text-align: center;
  margin: auto;
  background: #04162E;
  scroll: none;
}
.container{
  height: 100vh;
  width: 100vw;
  //background-color: #e0cccc;
  background-color: #04162E;
}
.saveBlog{
  //float:right;
  z-index: 999;
}
.saveBlog .el-button{
  color: white;
}
.editBlog{
  width: 80vw;
  height: 80vh;
  margin: 50px auto;
  opacity: 0.8;
}
.mavon-editor{
  height:800px;

}

</style>
