<template>
    <el-header>
        <el-menu  mode="horizontal" active-text-color="#fdc208" text-color="#ffffff"  background-color="#5a4c4c" :router="true" >
        <el-menu-item class="submenu" :index="'/blogCenter/blogHome'">博客大厅</el-menu-item>
        <el-menu-item class="submenu" :index="'/blogCenter/classify'">分类</el-menu-item>
        <el-menu-item style="left: 30%">
            <el-input class="search-input" placeholder="请输入博客关键字" v-model="searchKey" @keyup.enter.native="searchBlog" >
            <el-button  type="danger" slot="append"
            icon="el-icon-search" @click="searchBlog">
            </el-button>
            </el-input>
        </el-menu-item>
        <el-menu-item style="left: 50%" v-if="loginState">
            <el-dropdown @command="userRouter">
            <span class="el-dropdown-link">
                <el-image :src="url+userInfo.headPortrait" v-if="userInfo.headPortrait"></el-image>
                <el-image :src="require('../../../static/image/user.png')" v-else></el-image>
            </span>
            <el-dropdown-menu slot="dropdown" split-button="true">
                <el-dropdown-item command="userCenter">个人中心</el-dropdown-item>
                <el-dropdown-item command="myBlog">我的博客</el-dropdown-item>
                <el-dropdown-item>内容管理</el-dropdown-item>
                <el-dropdown-item command="layout">退出登录</el-dropdown-item>
            </el-dropdown-menu>
            </el-dropdown>
        </el-menu-item>
        <el-menu-item style="left: 55%">
            <el-button type="danger" style="border-radius: 20px" @click="$router.push('/writeBlog')">写博客</el-button>
        </el-menu-item>
        </el-menu>
  </el-header>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name: "BlogCenter",
  data(){
    return{
      searchKey:''
    }
  },
  computed:{
     ...mapState(['loginState','userInfo'])
  },
  methods:{
    searchBlog(){
      let routeUrl=this.$router.resolve({path:'/search',query:{searchKey:this.searchKey,store:JSON.stringify(this.$store.state) }})
      window.open(routeUrl.href, '_blank');
    },
    userRouter(instructions){
      if(instructions==='myBlog'){
        this.$router.push('/myBlog')
      }else if(instructions==='layout'){
        this.layout()
      }else if(instructions === 'userCenter'){
        this.$router.push('/userCenter')
      }
    },
    //退出登录
    layout(){
      console.log("退出登录")
      //销毁session
      window.sessionStorage.clear();
      this.$router.push('/home')
    },
  }
}
</script>

<style scoped Lang="less">
.el-header{
  padding: 0;
}
.el-menu span{
  font-size: 30px;
  left: 60%;
  margin-top: 15px;
}
.el-dropdown-link .el-image{
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.submenu{
  left: 10%;
}

.search-input{
  border-radius: 10px;
  width: 500px;
}
</style>