<template>
<div class="container" @scroll="scroll($event)" ref="scrollRef">
    <el-backtop target=".container">
        <i class="el-icon-caret-top"></i>
    </el-backtop>
    <el-header> 
        <PriNavTop></PriNavTop>
    </el-header>
    <div class="search" :style="scrollTop>60?{'position':'fixed','top':'0'+'px','z-index':'999','margin-top':'0'}:{}">
        <el-input class="search-input" placeholder="查点什么.." clearable v-model="searchKey" @keyup.enter.native="searchBlog">
            </el-input>
        <el-button @click="searchBlog">点一下</el-button>
    </div>
    <el-main>
        <el-row>
            <el-col :xs="15" :sm="14" :md="13" :lg="12" :xl="11" :offset="5">
                <ul class="blog-list">
                    <li class="blog-list-item" v-for="(item,index) in blogList" :key="index" >
                        <div class="blog-title" @click="blogView(item.BLOG_ID)">
                          <p>{{item.TITLE}}</p>
                        </div>
                        <div class="blog-intro">
                          <p>{{item.CONTENT+'...'}}</p>
                        </div>
                        <div class="blog-info">
                            <span class="blog-info-authorName" v-if="((item.AUTHOR_NAME).length<=8&&(item.AUTHOR_NAME!==null))">{{item.AUTHOR_NAME}}</span>
                            <span class="blog-info-authorName" v-else>{{ (item.AUTHOR_NAME).slice(0,8)+'...'}}</span>
                            <span class="blog-info-blogTime" v-if="(item.TIME).length <= 10">{{item.TIME}}</span>
                            <span class="blog-info-blogTime" v-else>{{ (item.TIME).slice(0,10)}}</span>
                        </div>
                    </li>
                </ul>
                <div class="loading">
                    <i v-if="blogLimit"  class="el-icon-loading" style="font-size:50px;"></i>
                    <p v-else>到底了哦..</p>
                </div>
            </el-col>
            <el-col :xs="2" :sm="3" :md="4" :lg="5" :xl="3" :offset="1"><div class="sidebar"></div></el-col>
        </el-row>
    </el-main>
</div>
</template>

<script>
import {mapState} from 'vuex'
import PriNavTop from '../../pageItem/Blog/PriNavTop.vue'
export default {
  data(){
    return{
      searchKey:'',
      blogList:[],
      blogLimit:true,
      total:'',
      scrollTop:0,
    }
  },
  components:{PriNavTop},
  computed:{
    ...mapState(['loginState','userInfo'])
  },
  created() {
    this.searchKey = this.$route.query.searchKey
    console.log("获取到的store",JSON.parse(this.$route.query.store) );
    this.$store.replaceState(Object.assign(this.$store.state,JSON.parse(this.$route.query.store)));
    this.searchBlog()
  },
  methods:{
    async searchBlog(){
      const {data : res} = await this.$http.post('/search',{searchKey:this.searchKey,total:0})
      console.log("res",res)
      if(res.meta.status === 201){
        // res.data.blogList.forEach((item)=>{
        //   this.$set(this.blogList,this.blogList.length,item)
        // })
        this.blogList = res.data.blogList
      }
      this.blogLimit = res.data.blogLimit

    },
    blogView(blogId){
      let routeUrl=this.$router.resolve({path:'/blogView',query:{blogId:blogId}})
      window.open(routeUrl.href, '_blank');
    },
    load(){
      if(this.blogLimit === true){
        setTimeout(async()=>{
          const {data : res} = await this.$http.post('/search',{searchKey:this.searchKey,total:this.blogList.length})
          if(res.meta.status === 201){
            res.data.blogList.forEach((item)=>{
              this.$set(this.blogList,this.blogList.length,item)
            })
          }
          this.blogLimit = res.data.blogLimit
        },1000)
      }
    },
    scroll(){
      const sh = this.$refs['scrollRef'].scrollHeight // 滚动条高度
      const st = this.$refs['scrollRef'].scrollTop // 滚动条距离顶部的距离
      const ch = this.$refs['scrollRef'].clientHeight // 滚动条外容器的高度
      this.scrollTop = st
      if(st+ch === sh){
        this.load()
      }

    },
  }
}
</script>

<style scoped Lang="less">
.container{
  width: 100%;
  background-color: rgb(242,242,242);
  overflow-y: scroll;
  height: 100%;
}

.search .el-input{
  width: 50vw;
}
.search .el-button{
  height: 50px;
  background-color: rgb(254,107,0);
  color: white;
}
.search{
  width: 100%;
  height: 65px;
  background-color: rgb(255,255,255);
  justify-content: center;
  display: flex;
  align-items: center;
  margin-top: 5px;
}
::v-deep .el-input__inner{
  height: 50px;
  background-color:rgb(242,242,242) ;
}
.main{
  width: 90vw;
  margin: 30px auto;

}
.blog-list{
  /* height: 50vh; */
  margin: 0 auto;
  /* background-color: white; */
  box-shadow: 1px 1px 3px #999999 ;
  overflow: auto;
  /* height:500px; */

}
.blog-list-item{
  width: 100%;
  height: 130px;
  margin: 1px auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  background-color: white;
  padding-left: 10px;
}
.blog-title{
  font-size: 20px;
  cursor: pointer;
  font-weight: 600;
}
.blog-title:hover{
  color: red;
}
.blog-title>p{
  padding-left: 10px;
}
.blog-intro{
  color: #999999;
}
.blog-intro>p{
  padding-left: 10px;
}
.blog-info{
  width: 100%;
  /* flex-direction:row-reverse; */
  display: flex;
  /* justify-content: end; */
  position: relative;
}
.blog-info-authorName{
  position: absolute;
  right: 10%;
  width: 20%;
  /* height: 15%; */
  color: #999999;
  cursor: pointer;
}
.blog-info-authorName:hover{
  color: red;
}
.blog-info-blogTime{
  position: absolute;
  right: 5%;
  color: #999999;
}


.sidebar{
  background-color: white;
  height: 50vh;
}
.el-col {
  border-radius: 4px;
}
.loading{
  width: 100%;
  /* border: 1px solid red; */
  background-color: rgb(230,230,230);
  margin: 0 auto;
  font-size: 30px;
  color: #999999;
  text-align: center;
}


</style>
